"use client";
import { useEffect, useState } from "react";
import { useParams } from "next/navigation";

export default function ItemDetails() {
    const params = useParams();
    const [product, setProduct] = useState(null);

    useEffect(() => {
        fetch(`https://server-sage-gamma.vercel.app/api/products/${params.id}`)
            .then(res => res.json())
            .then(data => setProduct(data));
    }, [params.id]);

    if (!product) return <p className="p-10">Loading...</p>;

    return (
        <div className="p-10 max-w-4xl mx-auto">
            <img src={product.image} alt={product.title} className="w-full h-96 object-cover rounded mb-4" />
            <h1 className="text-3xl font-bold mb-2">{product.title}</h1>
            <p className="text-gray-700 mb-2">{product.fullDesc}</p>
            <p className="text-green-600 font-semibold mb-2">Price: ${product.price}</p>
            <p className="text-gray-500 mb-4">Added on: {new Date(product.date).toLocaleDateString()}</p>
            <button className="px-4 py-2 bg-green-600 text-white rounded hover:bg-green-700" onClick={() => history.back()}>
                Back
            </button>
        </div>
    );
}
